.relatedproducts{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 10px;
    height: 80vh;
    margin-bottom: 200px;
}
.relatedproducts h1{
    color: #171717;
    font-size: 50px;
    font-weight: 600;
    
} 
.relatedproducts hr{
    width: 200px;
    height: 6px;
    border-radius: 10px;
    background: #252525;
}
.relatedproducts-item{
    margin-top:40px ;
    display: flex;
    
    
    gap: 8px;
    
    
}
.relatedproducts-item img{
    height: 300px;
}